.page1 {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

/* Background Video */
.background-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -3; /* Keep the video at the very back */
}

/* Background Color (Above Video, Below Elements) */
.background-color {
  position: absolute;
  top: 50vh; /* Start from the middle to the bottom */
  left: 0;
  width: 100%;
  height: 50vh; /* Cover the lower half of the viewport */
  background-color: #df9542;
  z-index: -2; /* Place it above the video, below the rest */
}

.social-media {
  position: absolute;
  top: 0;
  left: calc(50% - 545px);
  transform: translateX(-50%);
}

/* Spurpunk Title (Red) */
.spurpunk-title {
  position: absolute;
  top: calc(199.5px - 175px / 2);   
  left: calc(50% - 536px / 2);      
  width: 536px;
  height: 175px;
}

/* Skeleton Frame (Blue) */
.skeleton-frame {
  position: absolute;
  top: 300px;
  left: 41%;
  transform: translateX(-50%);
  width: 280px;
  height: auto;
}

/* Trailer Ribbon (Green) */
.trailer-ribbon {
  position: absolute;
  top: calc(300px + 185px);  /* Positioned right below the skeleton frame */
  left: 39%;
  transform: translateX(-50%);
  width: auto;  /* Dynamic width */
  max-width: 70vw;  /* Restrict to 70% of viewport */
  height: auto;
}

/* Text under Trailer Ribbon (Pink) */
.are-you-spurpunk {
  position: absolute;
  top: calc(300px + 280px + 65px);  /* Placed under the trailer ribbon */
  left: 28%;
  transform: translateX(-50%);
  font-size: 2.5rem;
  text-align: center;
  color: #fff;
}

/* Map Cactus2 Icon */
.map-cactus2 {
  position: absolute;
  top: 300px;
  left: 40px;
  width: 159px;
  height: 160px;
  z-index: -1;
}

/* Compass Icon */
.compass-icon {
  position: absolute;
  top: 400px;
  left: 210px;
  width: 200px;
  height: 200px;
}

/* Map Grass Icon */
.map-grass-icon {
  position: absolute;
  top: 671px;
  left: 200px;
  width: 53px;
  height: 94px;
}

/* Duplicate Map Grass Icon */
.map-grass-icon-duplicate {
  position: absolute;
  top: 500px;
  left: 900px;
  width: 53px;
  height: 94px;
}

/* Map Cactus Icon */
.map-cactus-icon {
  position: absolute;
  top: 300px;
  left: 1017.5px;
  width: 153px;
  height: 136px;
  z-index: -1;
}

/* Page 1 Dotted Line */
.page1-dotted-line {
  position: absolute;
  top: calc(784px - 750px / 2);
  left: calc(50% - 1000px / 2);
  width: 882px;
  height: 380px;
  z-index: -2;
}

/* Play Trailer Arrow */
.play-trailer-arrow {
  position: absolute;
  top: calc(797.5px - 505px / 2); 
  left: calc(50% - 120px / 2);
  width: 60px;
  height: 63px;
}

/* Arrow Down */
.arrow-down {
  position: absolute;
  top: calc(868px - 500px / 2);
  left: calc(50% + (1009px - 1220px / 2));
  width: 92px;
  height: 88px;
}